body {
  background: #000;
  color: #ffffff;
  overflow: hidden;
}

label {
  color: #ffffff;
  font-size: 1.3rem;
}

input {
  margin: 0 !important;
}

.wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.alert-box {
  margin-top: 0;
}

.booking-current {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}

.booking-current__name {
  font-size: 65px;
  color: #d95e61;
}

.booking-current__info {
  font-size: 36px;
  color: #d95e61;
}

.booking-current__status {
  color: #ffffff;
  font-size: 65px;
}

.booking-current__status-info {
  color: #ffffff;
  font-size: 36px;
}

.booking-list {
  display: inline-block;
  margin: 0 auto;
  width: 935px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;
}

.booking-list__item-time {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  font-size: 23px;
}

.booking-list__item {
  position: relative;
  overflow: hidden;
  height: 95px !important;
  width: 95px !important;
  background: #008033;
  margin-right: 10px;
  border-radius: 10px;
}

.booking-list__item--active {
  background: red;
}

.booking-list__item:last-child {
  margin-right: 0;
}

.booking-form {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
  height: 110%;
  width: 100%;
  background: #000;
  transition: 0.7s cubic-bezier(0.71, -0.35, 0.58, 1);
}

.booking-form--active {
  top: 0;
}

.booking-form form {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}

.booking-form__row {
  min-height: 110px;
}

.booking-column:first-child {
  border-right: 3px solid #ffffff;
}

.booking-info-close {
  width: 40px;
  height: 40px;
  border: 3px solid #ffffff;
  border-radius: 50%;
  position: relative;
  margin: 25px;
  float: right;
  cursor: pointer;
  transition: 0.25s ease-in;
}

.booking-info-close:focus, .booking-info-close:active {
  transform: rotate(-180deg);
}

.booking-info-close__line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: #ffffff;
  top: 50%;
  transition: 0.7s ease-out;
  transform: translateY(-50%) rotate(180deg);
}

.booking-info {
  position: absolute;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}

.booking-info-content {
  opacity: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.booking-info--leave {
  animation: modal-leave 0.5s ease-in-out;
  animation-fill-mode: forwards;
}

.booking-info--active {
  display: block;
  animation: modal-enter 0.5s ease-in-out;
}

.booking-info--active .booking-info-content {
  opacity: 1;
}

.booking-info--active .booking-info-close__line {
  transform: translateY(-50%) rotate(45deg);
}

.booking-info--active .booking-info-close__line:last-child {
  transform: translateY(-50%) rotate(-45deg);
}

.booking-info__title {
  text-align: right;
  font-weight: initial;
  font-size: 30px;
  border-right: 1px solid #ffffff;
}

.booking-info-container {
  padding-top: 30px;
  padding-left: 30px;
}

.booking-info__data {
  font-size: 30px;
  font-weight: 100;
}

.booking-info__header {
  color: #ffffff;
}

@keyframes modal-enter {
  from {
    opacity: 0;
    transform: rotateX(-60deg);
  }
  to {
    opacity: 1;
    transform: rotateX(0);
  }
}

@keyframes modal-leave {
  from {
    opacity: 1;
    transform: rotateX(0);
  }
  to {
    opacity: 0;
    transform: rotateX(-60deg);
  }
}

.button-default {
  display: inline-block;
  background: transparent;
  border: 2px solid #ffffff;
  padding: 10px;
  width: 200px;
  height: 50px;
  outline: none;
  overflow: hidden;
}

.button-default:focus, .button-default:hover, .button-default:focus, .button-default:hover, .button-default:disabled {
  border: 2px solid #ffffff !important;
  background-color: transparent !important;
}

.date-time {
  text-align: center;
  font-weight: 100;
}

.date-time__currentTime {
  font-size: 40px;
}

.date-time__currentTime-hours {
  font-size: 100px;
}

.date-time__currentTime-mins {
  font-size: 74px;
  vertical-align: text-bottom;
  padding-left: 10px;
}

.date-time__currentDay {
  color: #ffffff;
  font-size: 40px;
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 400;
}

.loader {
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.loader-canvas {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}

.header {
  padding-top: 30px;
  padding-left: 30px;
}

.header-title {
  color: #ffffff;
  font-weight: 400;
  font-size: 50px;
  text-transform: capitalize;
}

.effect-click {
  position: absolute;
  width: 50px;
  height: 50px;
  background: #ffffff;
  opacity: 0;
  border-radius: 50%;
  animation: click-radius-expand 0.7s cubic-bezier(0.06, 0.39, 0.71, 0.93) forwards;
}

@keyframes click-radius-expand {
  from {
    transform: scale(0);
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: scale(10);
  }
}
